@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&display=swap');
*{
	margin: 0;
	padding: 0;
	border: 0;
	outline: 0;
	box-sizing: border-box;
	list-style: none;
	text-decoration: none;
}

:root{
	--bg:  #1f1f38;
	--bg-variant: #2c2c6c;
	--primary: #4db5ff;
	--primary-variant: rgba(77,181,255,0.4);
	--white: #fff;
	--light: rgba(255, 255, 255, 0.6);

	--container-lg: 75%;
	--container-md: 86%;
	--container-sm: 90%;

	--transition: all 400ms ease;
}

html{
	scroll-behavior: smooth;
}

::webkit-scrollbar{
	display: none;
}

body{
	font-family: 'Montserrat', sans-serif;
	background: var(--bg);
	color: var(--white);
	line-height: 1.7;
}

.container{
	width: var(--container-lg);
	margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5{
	font-weight: 500;
}

h1 {
	font-size: 2.5rem;
}

section {
	margin-top: 5rem;
}

section > h2, section > h5 {
	text-align: center;
	color: var(--light);
}

section > h2{
	color:  var(--primary);
	margin-bottom: 2rem;
}

.text-light {
	color: var(--light);
}

a {
	color: var(--primary);
	transition: var(--transition);
}

a:hover {
	color: var(--white);
}

.btn {
	width: max-content;
	display: inline-block;
	color: var(--primary);
	padding: 0.75rem 1.2rem;
	border-radius: 0.4rem;
	cursor: pointer;
	border: 1px solid var(--primary);
	transition: var(--transition);
}

.btn:hover {
	background: var(--white);
	color: var(--bg);
	border-color: transparent;
}

.btn-primary {
	background: var(--primary);
	color: var(--bg);
}

img {
	display: block;
	width: 100%;
	object-fit: cover;
}

/*media query*/
@media screen and (max-width:  1024px) {
	.container {
		width: var(--container-md);
	}

	section {
		margin-top: 6rem;
	}
}

@media screen and (max-width:  600px) {
	.container {
		width: var(--container-sm);
	}

	section > h2 {
		margin-top: 2rem;
	}
}





















































